import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const InitialRegistration = () => {
  const navigate = useNavigate();
const [formData, setFormData] = useState({
    // userId:'',
    name: '',
    birthDate: '',
    address: '',
    phoneNumber: '',
    email: '',
    myNumber: '',
    role: 'parent'  
  });

  // axios.defaults.baseURL = process.env.REACT_APP_API_BASE_URL;

  const handleChange = async (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
      [e.target.name]: e.target.value,
    });
  };

  const handleRoleChange = (e) => {
    setFormData({
      ...formData,
      role: e.target.value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post('/api/authApi/register', formData);
      // const response = await axios.post('authApi/register', formData);
      if (response.data) {
        alert('');
        navigate('/');
      } else {
        alert(response.data.message);
      }
    } catch (error) {
      console.error('Error during registration:', error);
      alert('');
    }
  };

  return (
    <div>
      <h2></h2>
      <form onSubmit={handleSubmit}>
        <label>:
          <input type="text" name="name" value={formData.name} onChange={handleChange} required />
        </label><br />
        <label>:
          <input type="Date" name="birthDate" value={formData.birthDate} onChange={handleChange} required />
        </label><br />
        <label>:
          <input type="text" name="address" value={formData.address} onChange={handleChange} required />
        </label><br />
        <label>:
          <input type="tel" name="phoneNumber" value={formData.phoneNumber} onChange={handleChange} required />
        </label><br />
        <label>:
          <input type="email" name="email" value={formData.email} onChange={handleChange} required />
        </label><br />
        <label>:
          <input type="text" name="myNumber" value={formData.myNumber} onChange={handleChange} required />
        </label><br />
        <div>
              <input
                type="radio"
            id="guardian"
                name="role"
                value="parent"
                checked={formData.role === 'parent'}
                onChange={handleRoleChange}
              /> 
                        <label htmlFor="guardian"></label>
            </div>
            <div>
              <input
                type="radio"
                            id="admin"
                name="role"
                value="admin"
                checked={formData.role === 'admin'}
                onChange={handleRoleChange}
              /> 
              <label htmlFor="admin"></label>
            </div>
            <div>
              <input
                type="radio"
                            id="supporter"
                name="role"
                value="supporter"
                checked={formData.role === 'supporter'}
                onChange={handleRoleChange}
              /> 
                        <label htmlFor="supporter"></label>
            </div>
            <label>:
            <select>
        <option ></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
        </select><br />
        <label>ID:
          <input />
        </label><br />
        </label>
        <button type="submit"></button>
      </form>
    </div>
  );
};

export default InitialRegistration;
